import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import * as ui_actions from 'src/redux/actions/ui'
import api_actions from 'src/redux/actions/api'
import { StyledPage } from '../../components/Layout.styled'
import Search from '../../components/Search'
import ViewWordResults from './ViewWordResults'
import ViewWordHome from './ViewWordHome'

class ViewWord extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
    props.ui_actions.RX__search_title('Thesaurus Search:')
  }

  render() {
    //window.doneLoading('render')
    let { word_chunks, word_input } = this.props
    /*
     * Verify content:
     */
    let PageContent = null
    if (word_input) {
      PageContent = <ViewWordResults that={this} />
    } else {
      PageContent = <ViewWordHome that={this} />
    }
    /*
     * Render content:
     */
    return (
      <>
        <Search
          {...this.props}
          className={(!word_input || !word_chunks.length) ? 'Search SearchHome' : 'Search'}
          domains={false}
          title="Another word for..."
          placeholder="..."
          cue={[
            <span key="1">The most comprehensive and accurate <span className="color-accent u">API</span>&nbsp;</span>,
            <span key="2">for <b>N</b>atural <b>L</b>anguage <b>P</b>rocessing applications.</span>,
          ]}
        />
				<StyledPage>{PageContent}</StyledPage>
			</>
    )
  }
}

const mapDispatchToProps = dispatch => {
  return {
    ui_actions: bindActionCreators(ui_actions, dispatch),
    api_actions: bindActionCreators(api_actions, dispatch),
  }
}

const mapStateToProps = function(state) {
  return {
    word_poss: state.word.poss,
    word_input: state.word.input_str,
    word_chunks: state.word.chunks,
  }
}

export default connect(mapStateToProps, mapDispatchToProps)((ViewWord))
