import React from 'react'
import Hint from 'src/components/Hint'
import PosWord from './PosWord'
import { faAngleUp, faEllipsisH } from '@fortawesome/pro-solid-svg-icons'
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome'

export default class extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      advanced: props.expand || false,
    }
  }

  render() {
    let { pos, row, api_actions } = this.props
    /*
     * Validate
     */
    if (!pos || !row.pos_dict || !row.pos_dict[pos]) {
      return null
    }
    let slist_pos = row.pos_dict[pos]
    /*
     * Predefined lists of words
     * info = [ 0-1 (bad-ok), 0-1 (ok-proper), 0-1 (ok-unknown) ]
     * ok = [ 1, 0, 0 ]
     */
    let ListOk = Object.entries(slist_pos).map((tuple) => {
      let [word, info] = tuple
      if (info.toString() === '1,0,0') {
        return (<PosWord api_actions={api_actions} key={word} word={word} />)
      }
    }).filter(val => !!val)
    let ListBad = Object.entries(slist_pos).map((tuple) => {
      let [word, info] = tuple
      if (info.toString() === '0,0,0') {
        return (<PosWord api_actions={api_actions} key={word} word={word} />)
      }
    }).filter(val => !!val)
    let ListProper = Object.entries(slist_pos).map((tuple) => {
      let [word, info] = tuple
      if (info[1] === 1) {
        return (<PosWord api_actions={api_actions} key={word} word={word} />)
      }
    }).filter(val => !!val)
    let ListUnknown = Object.entries(slist_pos).map((tuple) => {
      let [word, info] = tuple
      if (info[2] === 1) {
        return (<PosWord api_actions={api_actions} key={word} word={word} />)
      }
    }).filter(val => !!val)
    /*
     * Render simple version:
     */
    if (!this.state.advanced) {
      return (
        <div className={'ui-form-section simple'}>
					<div>
						{ListOk.length > 0 && <>
							<div className={'one'}><b>{pos}: </b></div>
							<div className="two posWords">{ListOk}</div>
						</>}
            <div
              className={'three'}
              onClick={() => {
                this.setState({ advanced: true })
              }}
            >
							<FA icon={faEllipsisH} className="faEllipsisH" />
						</div>
					</div>
				</div>
      )
    } else {
      /*
       * Render advanced version:
       */
      if (!ListOk.length && !ListBad.length && !ListProper.length && !ListUnknown.length) {
        return null
      }
      return (
        <div className={'ui-form-section advanced'}>
					<p className={'one'}>
						<b className="title">
							{pos}:
						</b>
					</p>

					<div className={'two'}>
						{ListOk.length > 0 &&
            <p>
							{/*<sub>{<>&#128512;</>}: </sub>*/}
              <Hint className={'color-accent-deep opacity50'}>OK/Good:</Hint>
							<span className="posWords">{ListOk}</span>
						</p>
            }
            {ListBad.length > 0 &&
            <p>
							{/*<sub>{<>&#128545;</>}: </sub>*/}
              <Hint className={'color-bad opacity50'}>Negative:</Hint>
							<span className="posWords">{ListBad}</span>
						</p>
            }
            {ListProper.length > 0 &&
            <p>
							{/*<sub>{<>&#127963;</>}: </sub>*/}
              <Hint className={'color-light opacity75'}>Proper:</Hint>
							<span className="posWords">{ListProper}</span>
						</p>
            }
            {ListUnknown.length > 0 &&
            <p>
							{/*<sub>{<>&#10067;</>}: </sub>*/}
              <Hint className={'color-light opacity75'}>Unknown sentiment:</Hint>
							<span className="posWords">{ListUnknown}</span>
						</p>
            }
					</div>

					<p
            className={'three'}
            onClick={() => {
              this.setState({ advanced: false })
            }}
          >
						<FA icon={faAngleUp} className="faAngleUp" />
					</p>
				</div>
      )
    }
  }
}
